'use strict'
if ( Meteor.isServer ) {
  const Future = require('fibers/future');
  const bodyParser = require('body-parser');
  const XMLHttpRequest = require("xmlhttprequest").XMLHttpRequest;
  const Fiber = require('fibers');

  Picker.middleware(bodyParser.urlencoded({ extended: false }));
  Picker.middleware(bodyParser.json());

  const DOMAIN = "gigu.auth0.com";
  const CLIENT_ID = "pHssjwxDj2G9Wa5LWseUzcOTIcutWUCo";
  const CLIENT_SECRET = "mf5ih9DIi7Y0BTb-0XNWB4Ld4c2Bwuo3AIpWhX2OpvEYRUzawpZtlkXOCqPYRS3k";
  const CALLBACK = process.env.ROOT_URL+"/callback";

  Meteor.methods({
    getAuthCredentials: function() {
      return {
      	domain: DOMAIN,
      	id: CLIENT_ID,
      	secret: CLIENT_SECRET,
      	cb: CALLBACK
      };
    },
    processAuthLogin: function(accessCode) {
    	check(accessCode, String);
    	const future = new Future();
    	const xmlhttp = new XMLHttpRequest();
      const url = "https://"+DOMAIN+"/oauth/token";
      // get access token
      xmlhttp.open("POST", url, true);
      xmlhttp.setRequestHeader("Content-Type", "application/json");
      xmlhttp.onreadystatechange = function() {
       if (xmlhttp.readyState === 4) {
         if (0 < xmlhttp.status/200 && xmlhttp.status/200 < 2) {
         		let response = JSON.parse(xmlhttp.responseText); 
         		response.domain = DOMAIN,
      			response.clientId = CLIENT_ID,
      			response.callback = CALLBACK
         		const xmlhttp2 = new XMLHttpRequest();

         		// get user profile
         		xmlhttp2.open("GET", "https://"+DOMAIN+"/userinfo", true);
         		xmlhttp2.setRequestHeader("Content-Type", "application/json");
         		xmlhttp2.setRequestHeader("Authorization", "Bearer "+response.access_token);
      			xmlhttp2.onreadystatechange = function() {
      				if (xmlhttp2.readyState === 4) {
      					if (0 < xmlhttp2.status/200 && xmlhttp2.status/200 < 2) {
      						let userInfo = JSON.parse(xmlhttp2.responseText);

      						Fiber(function() { 
	      						try {
	      							// save user to local db
							  			let acc = Accounts.createUser({
							  				username: userInfo.nickname,
								    		email: userInfo.email,
								    		password: "oauthUserPasswordDefault"
							  			});
							  			if (acc) {
							  				// send back user info to force login
							  				let newUser = Users.findOne(acc);
        								future.return({
        									username: newUser.username,
        									password: "oauthUserPasswordDefault"
        								});
							  			}
							  		} catch(err) {
							  			let existUser;
							  			// in case username exist use local db user
							  			if (err.reason === 'Username already exists.') {
							  				existUser = Users.find({username: userInfo.nickname}).fetch()[0];
          							future.return({
        									username: existUser.username,
        									password: "oauthUserPasswordDefault"
        								});
							  			}
							  		}
							  	}).run();
      					} else {
			         		future.return({
			         			error: JSON.parse(xmlhttp2.responseText).error
			        		});
			         	}
      				}
      			}
      			xmlhttp2.send();
         } else {
         		future.return({
         			error: JSON.parse(xmlhttp.responseText).error
        		});
         }
       }
      }
      xmlhttp.send(JSON.stringify({
       grant_type: "authorization_code",
       client_id: CLIENT_ID,
       client_secret: CLIENT_SECRET,
       code: accessCode,
       redirect_uri: CALLBACK
      }));

      return future.wait();
    },
    processAuthLogout: function() {
    	const future = new Future();
    	const xmlhttp = new XMLHttpRequest();
      const url = "https://"+DOMAIN+"/v2/logout?returnTo="+encodeURIComponent(process.env.ROOT_URL+"/sign-in")+"&client_id="+CLIENT_ID;
      // logout auth0 user
      xmlhttp.open("GET", url, true);
      xmlhttp.setRequestHeader("Content-Type", "application/json");
      xmlhttp.send();
    }
   });
}
